import axios from "axios";
import React, { useRef, useState } from "react";

import AddIcon from '@mui/icons-material/Add';

function Iparea(props) {
  const [noteTask, setNoteTask] = useState({
    tittle: "",
    desc: "",
  });
  const inpVal = useRef();
  const inpVal2 = useRef();

  function handleChange(event) {
    const { name, value } = event.target;
    setNoteTask((pv) => {
      return {
        ...pv,
        [name]: value,
      };
    });
  }

  const userId = localStorage.userId;

  function submitData(e) {
    // console.log(props.submitdata());
    let userId = localStorage.userId
    let tittle = noteTask.tittle
    let desc = noteTask.desc

    axios.post(`/api/addnote`,{userId,tittle, desc})
    .then(res => {
      // console.log(res);
        setNoteTask({
            tittle : "",
            desc : ""
        })
        props.addTsk(res.data)
    }).catch(err => console.log(err))
    e.preventDefault();
  }
  return (
    <div className="row justify-content-center">
      <div className="col-md-8 ">
        <div className="card bg-light rounded">
          <div className=" card-body rounded">
            <form className="frm1 bg-light">
              <input
                className="myinp1 p-3"
                name="tittle"
                type="text"
                value={noteTask.tittle}
                onChange={handleChange}
                ref={inpVal}
                placeholder="Tittle"
              />

              <textarea
                className="mytxt1 p-3 bg-light"
                name="desc"
                type="text"
                value={noteTask.desc}
                onChange={handleChange}
                ref={inpVal2}
                placeholder="Take a Note"
                row="3"
              />
              

              <button
                type="button"
                className="btn btn-floating btn-warning"
                onClick={submitData}
              >
                <AddIcon />
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Iparea;
