import React, { useRef, useState } from "react";
import { Navigate, Route, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";

import DoneIcon from "@mui/icons-material/Done";

function EditNote() {
  const location = useLocation();
  const { id } = location.state;
  // console.log(location.state);
  let navi = useNavigate();

  const [noteTask, setNoteTask] = useState({
    tittle: "",
    desc: "",
  });

  const [toast, setToast] = useState(false);
  const inpVal = useRef();
  const inpVal2 = useRef();

  React.useEffect(() => {
    console.log("hi");
    axios
      .get(`/api/editnote/${id}`)
      .then((res) => {
        setNoteTask(res.data);
        // return (
        setToast(true);
        // );
      })
      .catch((err) => console.log(err));
  }, []);

  function handleChange(event) {
    const { name, value } = event.target;
    setNoteTask((pv) => {
      return {
        ...pv,
        [name]: value,
      };
    });
  }

  function updateData(e) {
    // console.log(props.submitdata());
    let tittle = noteTask.tittle;
    let desc = noteTask.desc;

    axios
      .put(`/api/editnote/${id}`, { tittle, desc })
      .then((res) => {
        console.log("res received");
        // props.addTsk(res.data)

        // const redirect = () => {
        navi("/note");
        // };
      })
      .catch((err) => console.log(err));
    e.preventDefault();
  }
  
  return (
    <React.Fragment>
      <div className="row justify-content-center">
        <div className="col-md-8 ">
          <div className="card bg-light rounded">
            <div className=" card-body rounded">
              <form className="frm1 bg-light">
                <input
                  className="myinp1 p-3"
                  name="tittle"
                  type="text"
                  value={noteTask.tittle}
                  onChange={handleChange}
                  ref={inpVal}
                  placeholder="Tittle"
                />

                <textarea
                  className="mytxt1 p-3 bg-light"
                  name="desc"
                  type="text"
                  value={noteTask.desc}
                  onChange={handleChange}
                  ref={inpVal2}
                  placeholder="Take a Note"
                  row="3"
                />

                <button
                  type="button"
                  className="btn btn-floating btn-warning"
                  onClick={updateData}
                >
                  <DoneIcon />
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>

     
    </React.Fragment>
  );
}

export default EditNote;
