import React, { useContext } from "react";
import WbIncandescentIcon from "@mui/icons-material/WbIncandescent";
import LoginIcon from "@mui/icons-material/Login";
import LogoutIcon from "@mui/icons-material/Logout";
import NoteAddIcon from "@mui/icons-material/NoteAdd";
import { Link } from "react-router-dom";

import { AuthContext } from "./../../context/auth-context";
import "./MainHeader.css";

function MainHeader(props) {
  const auth = useContext(AuthContext);

  const doLogout = (e) => {
    localStorage.clear();
    sessionStorage.clear();
    auth.logout();
  };

  return (
    <div className="container-fluid">
      <a className="navbar-brand  ms-5 text-warning " href="#">
        <WbIncandescentIcon className="fs-1 mt-2 pe-2" />
        <span className=" fs-5 text-warning fw-bold">Keeper</span>
      </a>
      <button
        class="navbar-toggler"
        type="button"
        data-mdb-toggle="collapse"
        data-mdb-target="#navbarNavDropdown"
        aria-controls="navbarText"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <i class="fas fa-bars text-white"></i>
      </button>
      <div
        className="collapse navbar-collapse justify-content-end"
        id="navbarNavDropdown"
      >
        <ul className="navbar-nav d-flex align-items-center me-5">
          <li className="nav-item mb-sm-2">
            <Link
              to="/"
              type="button"
              className="btn btn-rounded btn-warning me-1 fw-bold"
              aria-current="page"
            >
              HOME
            </Link>
          </li>

          <li className="nav-item mb-sm-2">
            <Link
              to="/"
              type="button"
              className="btn btn-warning btn-rounded me-1 fw-bold"
            >
              ABOUT
            </Link>
          </li>
          {!auth.isLoggedIn && (
            <li className="nav-item mb-sm-2">
              <Link
                to="/register"
                type="button"
                className="btn btn-rounded btn-primary me-1 fw-bold"
              >
                SIGN IN
              </Link>
            </li>
          )}
          {!auth.isLoggedIn && (
            <li class="nav-item mb-sm-2">
              <Link
                to="/login"
                type="button"
                class="btn btn-sm btn-rounded btn-success me-2 fw-bold"
              >
                Login
                <LoginIcon className="ps-1" />
              </Link>
            </li>
          )}
          {auth.isLoggedIn && (
            <li className="nav-item mb-sm-2">
              <Link
                to="/note"
                type="button"
                className="btn btn-rounded btn-success me-1 fw-bold"
              >
                ADD NOTES
                {/*<NoteAddIcon className="ms-2" /> */}
              </Link>
            </li>
          )}
          {auth.isLoggedIn && (
            <li class="nav-item mb-sm-2">
              <button
                type="button"
                onClick={doLogout}
                className="btn btn-rounded btn-danger text-white me-1 fw-bold"
              >
                LOGOUT
                <LogoutIcon className="ps-1" />
              </button>
            </li>
          )}
          {/* <li class="nav-item dropdown">
              <a
                class="nav-link dropdown-toggle"
                href="#"
                id="navbarDropdownMenuLink"
                role="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Dropdown link
              </a>
              <ul
                class="dropdown-menu"
                aria-labelledby="navbarDropdownMenuLink"
              >
                <li>
                  <a class="dropdown-item" href="#">
                    Action
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" href="#">
                    Another action
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" href="#">
                    Something else here
                  </a>
                </li>
              </ul>
            </li> */}
        </ul>
      </div>
    </div>
  );
}

export default MainHeader;
