import React, { useEffect, useState } from "react";
import axios from "axios"
import NotesList from "../components/NotesList";
import Iparea from "../components/Iparea";

import './NotePage.css'

function NotesPage() {
  const [notes, setNotes] = useState([]);

  const [data, setData] = useState(null);

  // const getText = () => {}

      const [notesTask, setnotesTask] = useState([])

      const userId = localStorage.userId

    React.useEffect(() => {

      axios.get(`/api/addnote/${userId}`)
      .then(res => setnotesTask(res.data))
      .catch( err => console.log(err))
      
    },[]);

    function submitdata(newTask) {
      setnotesTask(pv => {
        return [...pv, newTask]
      })
    }

  function addNote(newNote) {
    setnotesTask(prevNotes => {
      // console.log(prevNotes);
      return [...prevNotes, newNote];
    });
    // console.log(notesTask);
  }

  const removeTask = (task) => {
    const newList = notesTask.filter(item => !(item._id === task._id))
    setnotesTask(newList)
    // console.log(notesTask);
  }

  return (
    <div className="perdiv">
      <Iparea addTask = {submitdata} addTsk={addNote} />
      {notesTask.map((listTsk, index) => {
        return(
         <NotesList 
           key={index}
           id={listTsk._id}
           tittle={listTsk.tittle}
           desc={listTsk.desc}
           removeTask={removeTask}
           addTask = {submitdata}
         />
        )
      })}
      <div className="clear"></div>
    </div>
  )
}

export default NotesPage;
