import React, { useContext, useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Navigate,
  Route,
  Routes,
} from "react-router-dom";

// import { faTwitter, faFontAwesome } from '@fortawesome/free-brand-svg-icons'

import MainNavigation from "./shared/components/Navigation/MainNavigation";
import HomePage from "./home/page/HomePage";
import "./App.css";
import MainFooter from "./shared/components/Footer/MainFooter";
import Register from "./User/Register/pages/Register";
import Login from "./User/Login/pages/Login";
import { AuthContext } from "./shared/context/auth-context";
import { useCallback } from "react";
import NotesPage from "./notes/pages/NotePage";
import EditNote from "./notes/components/EditNote";

function App() {
  const auth = useContext(AuthContext);

  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const login = useCallback(() => {
    setIsLoggedIn(true);
  }, []);
  const logout = useCallback(() => {
    setIsLoggedIn(false);
  }, []);

  // React.useEffect(() => {
  
    if (!!localStorage.userId) {
      auth.login();
      console.log("logged");
    }

  // }, []);
  
  let loginRoutes;

  if (isLoggedIn) {
    loginRoutes = (
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/note" element={<NotesPage />} />
        <Route path="/editnote" element={<EditNote />} />
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    );
  } else {
    loginRoutes = (
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/register" element={<Register />} />
        <Route path="/login" element={<Login />} />
        {/* <Route path="/editnote" element={<EditNote />} /> */}
        <Route path="*" element={<Navigate to="/login" replace />} />
      </Routes>
    );
  }

  return (
    <AuthContext.Provider
      value={{ isLoggedIn: isLoggedIn, login: login, logout: logout }}
    >
      <Router>
        <MainNavigation />
        <main>{loginRoutes}</main>
        <MainFooter />
      </Router>
    </AuthContext.Provider>
  );
}

export default App;
